<template>
	<div class="header-footer">
		<div class="container">
			<ul class="tab">
				<li v-for="items in list" class="active">
					<router-link :to="items.rtpath">{{items.rtname}}</router-link>
					<div class="xiangqing">
						<h2 v-text="items.name"></h2>
						<h2 v-text="items.xinpin"></h2>

						<ul>
							<li>
								<h2 v-text='items.chunbu'></h2>
								<p v-for='fenleis in items.fenlei'>{{fenleis}}</p>
								<h2 v-text='items.jiayou'></h2>
							</li>
							<li>
								<h2 v-text='items.mianbu'></h2>
								<p v-for='fenlei1s in items.fenlei1'>{{fenlei1s}}</p>
								<h2 v-text='items.taoshua'></h2>

							</li>
							<li>
								<h2 v-text='items.yanbu1'></h2>
								<p v-for='yanbus in items.yanbu'>{{yanbus}}</p>
								
							</li>
							<li>
								<img :src="items.src" alt="">
							</li>
							<div class="tupian">
								<img :src="items.xsrc" alt="">
							</div>
						</ul>
					</div>
				</li>	
			</ul>
		</div>
		<div class="downall">
			<div class="downall-first">
			
			</div>
		</div>
	</div>
</template>

<script>
import './../assets/css/normalize.css'
export default{
	name:"headerFooter",
	data(){
		return{
			list:[
				{
					"rtpath":"lists",
					"rtname":"",
					"name":"",
					"xinpin":"",
					"fenlei":['','','','','',''],
					"fenlei1":['','','','','',''],
					"yanbu":['','','',''],
					"chunbu":"",
					"mianbu":"",
					"yanbu1":"",
					"jiayou":'',
					"taoshua":"",
					"src":"../../static/image/F6599100U_01.jpg"
				},
				{
					"rtpath":"skincare",
					"rtname":"",
					"name":"",
					"xinpin":"",
					"fenlei":['','','','','/','',''],
					"fenlei1":['Y','','','','',''],
					"yanbu":['','','','',''],
					"chunbu":"",
					"mianbu":"",
					"yanbu1":"",
					"jiayou":'',
					"src":"../../static/image/q3.jpg"
					
				},
				{
					"rtpath":"purfume",
					"rtname":"",
					"name":"",
					"xinpin":"",
					"fenlei":['','',''],
					"fenlei1":['','','',''],
					"yanbu":[' '],
					"chunbu":"",
					"mianbu":"",
					"yanbu1":"",
					"src":"../../static/image/L7847900U.jpg"
				},
				{
					"rtpath":"star",
					"rtname":"",
					"name":"",
					"xinpin":"",
					"xsrc":"../../static/image/start_drpdown.jpg"
					
				},
				{
					"rtpath":"giftbox",
					"rtname":""
				},
				{
					"rtpath":"official",
					"rtname":""
				},
				{
					"rtpath":'member',
					"rtname":""
				}	
			],
		}
	},
	methods:{
		
	}
}
</script>

<style>
body{
	font-family:"SimHei,sans-serif";
}
.header-footer{
	background-color: #fff;
    width: 100%;
    height: 53px;
}
.header-footer .container{
	width: 100%;
  	margin:0 auto;
  	height: 53px;
}
.tab{
	width: 996px;
	margin: 0 auto;
	padding: 0 30px;
	height: 53px;
}
.tab>li{
	display: inline-block;
	list-style: none;
	float: left;
}
.tab>li a{
	cursor: pointer;
	position: relative;
    display: block;
    padding: 0 33px 4px 33px !important;
    font-size: 18px;
    line-height: 49px;
    color: #070707;
    text-transform: uppercase;
    letter-spacing: .5px;
}

.tab>li a:before{
	content: "";
    display: block;
    width: 5px;
    height: 5px;
    background: url(/static/images/icons-sprite-25x25.png) no-repeat -75px -75px transparent;
    position: absolute;
    top: 50%;
    left: -7px;
    margin-top: -5px;
    font-weight: bold;
}

.tab>li:first-child a:before{
	display: none;
}

.tab li a:hover {
	color: orange;
}
.xiangqing{
	width: 100%;
	height: 400px;
	background-color: white;
	position: absolute;
	z-index: 20;
	left: 0;
	display: none;
	border-top:2px solid #000;
}
.tab li:hover .xiangqing{
	display: block;
}
.tab li .xiangqing>h2{
	margin-left: 100px;
	font-size: 15px;
	line-height: 30px;
}
.tab li .xiangqing ul li{
	float: left;
	width: 150px;
	height: 300px;
	/*border:1px solid red;*/
	margin-left: 100px;
	list-style: none;
	font-size: 13px;
	line-height: 30px;
}
.tab li .xiangqing ul li p{
	font-size: 13px;
}
.tab li .xiangqing ul div{
	width: 300px;
	height: 300px;
	margin-left: 200px;
}
.tab li .xiangqing ul div img{
	position: absolute;
	left: 200px;
}
</style>


