<script>
	import axios from "axios"
	import '.././assets/css/normalize.css'
	import NavHeader from '@/components/navHeader'
	import Service from '@/components/service'
	import MyFooter from '@/components/myfooter'
	export default{
		name:"registers",
		data(){
			return{
				userId:"",
				userName:"",
				password:"",
				repassword:"",
				telmail:"",
				err_message1:false,
				err_message2:false,
				err_message3:false,
				telmailexist:false,
				telmailOk:false,
				pwdOk:false,
				repwdOk:false
			}
		},
		components:{
			NavHeader,
			Service,
			MyFooter
		},
		methods:{
			checktelmail(){
				var ereg = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
				var treg = /^1[34578]\d{9}$/;
				var param ={
					telmail: this.telmail
				}
				if(ereg.test(this.telmail) || treg.test(this.telmail)){
					console.log(1);
      				//
        			axios.get('/users/checktelmail',{params:param}).then((response)=>{
	          			var res = response.data;
	          			//
          				if(res.status == '0'){
          					console.log(res.status);
            				this.telmailOk = true;
            				this.telmailexist =false;
            				this.err_message1 =false;
            				console.log(res.msg);
          				}else{
           				//
            				if(res.status == '2'){
            					console.log(3);
            					this.telmailexist = true;
            					this.telmailOk =false;
            					this.err_message1 =false;
            				}
          				}
        			})
      				//
      			}else{
      				console.log(2);
         			this.err_message1 = true;
         			this.telmailOk =false;
         			this.telmailexist =false;
      			}
			},
			checkPwd(){
				var reg = /^.{6,10}$/;
				if(reg.test(this.password)){
					this.pwdOk =true;
       				this.err_message2= false;
      			}
      			//
      			else{
         			this.err_message2 = true;
         			this.pwdOk =false;
     			}
			},
			checkPwd2(){
       			if(this.password != this.repassword){
        			this.err_message3 = true;
         			this.repwdOk =false;
      		 	}
       			else{
        			this.repwdOk =true;
       				this.err_message3= false;
       			}
			},
			checkname(){
				
			},
			register(){
				// console.log(this);
				//Post//cookie
        		axios.post('/users/register',{telmail: this.telmail,password: this.password,userName: this.userName}).then((response)=>{
          			var res = response.data;
          			if(res.status == '0'){
            			// 
           				 console.log(res.msg);
            			//
            			this.$router.push({path:'/',query:{'status':1,'telmail':this.telmail}})
          			}else{
           
         			}
        		})
			}
		}
	}
</script>
